import {
  getCurrentInstance, SetupContext,
} from '@vue/composition-api';

const useFormCheck = ({ root, refs }:SetupContext) => {
  const formCheck = (form:string) => {
    let pass;
    const comp = refs[form];

    if (comp.formCheck) {
      comp.formCheck();
    } else {
      comp.validate((valid:boolean) => {
        pass = valid;
      });
    }
    return pass;
  };

  const clearCheck = async (form:string) => {
    // 必须使用nextTick保证在弹窗出现后执行
    await root.$nextTick();
    const comp = refs[form];
    if (!comp) return;
    if (comp.clearCheck) {
      comp.clearCheck();
    } else {
      comp.clearValidate();
    }
  };

  return {
    formCheck,
    clearCheck,
  };
};

export default useFormCheck;
